n<template>
    <div id="SingleBlog">
        <h1>{{blog.title}}</h1>
        <article>
            {{blog.content}}
        </article>
        <p>作者：{{blog.author}}</p>

        <ul>
            <li v-for="category in blog.categories">
                {{category}}
            </li>
        </ul>
        <button @click="deleteSingleBlog()"> 删除</button>
        <router-link :to="'/edit/'+id">编辑</router-link>
    </div>
</template>

<script>
    export default {
        name:'SingleBlog',
        data() {
            return {
                id:this.$route.params.id,
                blog:{}
            }
        },
        created() {
            this.$http.get('https://wd7651480835tarxam.wilddogio.com/posts/'+this.id+'.json')
            .then(function(data){
                
                return data.json()
            })
            .then(function(data){
                console.log(data)
                this.blog=data
            })
        },
        methods: {
            deleteSingleBlog(){
                this.$http.delete('https://wd7651480835tarxam.wilddogio.com/posts/'+this.id+'.json')
                .then(Response=>{
                    this.$router.push({path:'/'})
                })
            }
        },
    }
</script>

<style scoped>
    #SingleBlog {
        max-width: 960px;
        margin: 0 auto;
        padding: 20px;
        background: #eeeeee;
        border: 1px dotted #aaaaaa;
         margin-top: 100px;
    }
</style>


